import { Component, OnInit, OnDestroy, AfterViewInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { environment } from '../../../../environments/environment';
import { Store } from '@ngrx/store';
import { AppState } from '../../../interfaces';
import { Router } from '@angular/router';
import { AuthService } from '../../../core/services/auth.service';
import { AddressService } from '../../../core/services/address.service';
import { getAuthStatus } from '../../reducers/selectors';
import { Subscription } from 'rxjs/Subscription';

// require('select2/dist/js/select2.full');

declare var $:JQueryStatic;

@Component({
  selector: 'app-corp-sign-up',
  templateUrl: './corp-sign-up.component.html',
  styleUrls: ['./corp-sign-up.component.scss']
})
export class CorpSignUpComponent implements OnInit, OnDestroy {
  c_registerForm: FormGroup;
  formSubmit = false;
  title = environment.AppName;
  c_registerSubs: Subscription;
  countries: any
  states: any
  addressData: any;

  constructor(
    private fb: FormBuilder,
    private store: Store<AppState>,
    private router: Router,
    private authService: AuthService,
    private addrService: AddressService
  ) {
    // this.redirectIfCorpSignIn();
  }

  ngOnInit() {
    this.initForm();
    this.addrService.getCountries().subscribe((address) => {
      this.addressData = address;
      this.countries = $.map(this.addressData, function(ct){
        var str = {
          id: ct.id,
          text: ct.name
        }
        return str;
      });
    });
  }

  onSubmit() {
    const values = this.c_registerForm.value;
    const keys = Object.keys(values);
    this.formSubmit = true;
    console.log("sign up value", this.c_registerForm.value);

    if (this.c_registerForm.valid) {
      this.c_registerSubs = this.authService.c_register(values).subscribe(data => {
        const errors = data.errors;
        if (errors) {
          keys.forEach(val => {
            if (errors[val]) { this.pushErrorFor(val, errors[val][0]); };
          });
        } else{
          this.redirectIfCorpSignIn();
        }
      });
    } else {
      keys.forEach(val => {
        const ctrl = this.c_registerForm.controls[val];
        if (!ctrl.valid) {
          this.pushErrorFor(val, null);
          ctrl.markAsTouched();
        };
      });
    }
  }

  private pushErrorFor(ctrl_name: string, msg: string) {
    this.c_registerForm.controls[ctrl_name].setErrors({'msg': msg});
  }

  initForm() {
    const company_id = '';
    const company_name = '';
    const email = '';
    const password = '';
    const password_confirmation = '';
    const checkout_password = '';
    const checkout_password_confirmation = '';
    const company ='';
    const address1 = '';
    const address2 = '';
    const zip ='';
    const city ='';
    const contact_phone = '';
    const country_id = '';
    const state_id = '';
    const shipping_category_id = '1';
    const product_price_set_id = '1';


    this.c_registerForm = this.fb.group({
      'company_id': [company_id, Validators.required],
      'company_name': [company_name, Validators.required],
      'email': [email, Validators.required],
      'password': [password, Validators.required],
      'password_confirmation': [password_confirmation, Validators.required],
      'checkout_password': [checkout_password, Validators.required],
      'checkout_password_confirmation': [checkout_password_confirmation, Validators.required],
      'company': [company],
      'address1': [address1, Validators.required],
      'address2': [address2],
      'city': [city, Validators.required],
      'zip_code': [zip, Validators.required],      
      'contact_phone': [contact_phone, Validators.required],
      'country_id': [country_id, Validators.required],
      'state_id': [state_id, Validators.required],
      'shipping_category_id': [shipping_category_id],
      'product_price_set_id': [product_price_set_id]
    });
  }

  redirectIfCorpSignIn() {
    // this.store.select(getAuthStatus).subscribe(
    //   data => {
    this.router.navigateByUrl('/auth', '/signup');
    //   }
    // );
  }

  ngOnDestroy() {
    if (this.c_registerSubs) { this.c_registerSubs.unsubscribe(); }
  }

  countryChanged(e: any){
    this.c_registerForm.controls['country_id'].setValue(e.value);
    console.log(e.value);
    var ct: any;
    for (var index in this.addressData){
      if (this.addressData[index].id == e.value ){
        ct = this.addressData[index];
        break;
      }
    }
    this.states = $.map(ct.states, function(st){
      var str = {
        id: st.id,
        text: st.name
      }
      return str;
    });
    console.log(ct);
  }
  stateChanged(e: any){
    this.c_registerForm.controls['state_id'].setValue(e.value);
    console.log(e.value);
  }
}



